* {
    margin: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
@font-face {
    font-family: 'Satisfy';
    src: 
            local('Satisfy'), 
            url(https://fonts.google.com/?category=Handwriting&selection.family=Satisfy.woff) format('woff');
}
#app {
    font-family: 'Satisfy';
    color: darkslategray;
    font-size: 20px;
}
#root {
    width: 400px;
    height: 400px;
    margin: auto;
}
#app {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column nowrap;
        flex-flow: column nowrap;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    position: relative;
    width: 100%;
    height: 100%;
    background-color: #dfecec;
    border: 2px solid darkslategrey;
}
#header {
    padding: 10px;
    width: 50%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
}
#month {
    margin: 10px;
}
#monthDiv {
    position: absolute;
    background-color: #dfecec;
}
#monthDiv > div {
    height: 25px;
    padding: 5px;
}
#year {
    margin: 10px;
}
#yearDiv {
    position: absolute;
    width: 70px;
    height: 300px;
    overflow-y: scroll;
    background-color: #dfecec;
}
#yearDiv::-webkit-scrollbar-track {
    background-color: #dfecec;
    border: 1px solid darkslategray;
}
#yearDiv::-webkit-scrollbar {
	width: 12px;
	background-color: #F5F5F5;
}

#yearDiv::-webkit-scrollbar-thumb {
	background-color: darkslategray;
}
.yearVal {
    height: 25px;
    padding: 5px;
}


